md2-tooltip {
  pointer-events: none; }

.md2-tooltip {
  color: white;
  padding: 6px 8px;
  border-radius: 2px;
  font-size: 10px;
  margin: 14px;
  max-width: 250px;
  background: rgba(97, 97, 97, 0.9);
  word-wrap: break-word; }

.cdk-overlay-container, .cdk-global-overlay-wrapper {
  pointer-events: none;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%; }

.cdk-overlay-container {
  position: fixed;
  z-index: 1000; }

.cdk-global-overlay-wrapper {
  display: flex;
  position: absolute;
  z-index: 1000; }

.cdk-overlay-pane {
  position: absolute;
  pointer-events: auto;
  box-sizing: border-box;
  z-index: 1000; }
